"use client";

import React, { useState } from 'react';
import { useForm, SubmitHandler } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import * as z from 'zod';
import { Input } from '@/components/ui/input';
import { Button } from '@/components/ui/button';
import { Label } from '@/components/ui/label';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { RadioGroup, RadioGroupItem } from '@/components/ui/radio-group';
import { cn } from '@/lib/utils';

const formSchema = z.object({
  species: z.enum(['cao', 'gato'], {
    required_error: "Selecione uma espécie",
  }),
  weight: z.number({
    required_error: "Peso é obrigatório",
    invalid_type_error: "Peso deve ser um número",
  }).positive({ message: 'Peso deve ser maior que zero' }),
  dehydration: z.number({
    required_error: "Percentual de desidratação é obrigatório", 
    invalid_type_error: "Percentual deve ser um número",
  }).min(0, { message: 'Mínimo 0%' }).max(100, { message: 'Máximo 100%' })
});

type FormData = z.infer<typeof formSchema>;

interface CalculationResult {
  fluidDeficit: number;
  species: string;
  weight: number;
  dehydration: number;
}

export default function HomePage() {
  const [result, setResult] = useState<CalculationResult | null>(null);
  const [isCalculating, setIsCalculating] = useState(false);

  const { 
    register, 
    handleSubmit, 
    formState: { errors },
    setValue,
    watch
  } = useForm<FormData>({
    resolver: zodResolver(formSchema),
    defaultValues: {
      species: 'cao',
      weight: undefined,
      dehydration: undefined
    }
  });

  const watchedSpecies = watch('species');

  const onSubmit: SubmitHandler<FormData> = async (data) => {
    setIsCalculating(true);
    
    try {
      // Simulate a brief calculation delay for better UX
      await new Promise(resolve => setTimeout(resolve, 300));
      
      const fluidDeficit = data.weight * (data.dehydration / 100) * 1000;
      
      setResult({
        fluidDeficit,
        species: data.species === 'cao' ? 'Cão' : 'Gato',
        weight: data.weight,
        dehydration: data.dehydration
      });
    } catch (error) {
      console.error("Erro no cálculo:", error);
      setResult(null);
    } finally {
      setIsCalculating(false);
    }
  };

  const resetCalculation = () => {
    setResult(null);
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-slate-50 to-slate-100 dark:from-slate-900 dark:to-slate-800">
      <div className="container mx-auto px-4 py-8 max-w-2xl">
        {/* Header */}
        <div className="text-center mb-8">
          <h1 className="text-4xl font-bold text-slate-900 dark:text-slate-100 mb-2">
            Calculadora de Fluidoterapia
          </h1>
          <p className="text-lg text-slate-600 dark:text-slate-400">
            Para Cães e Gatos
          </p>
          <div className="w-24 h-1 bg-slate-900 dark:bg-slate-100 mx-auto mt-4 rounded-full"></div>
        </div>

        {/* Main Calculator Card */}
        <Card className="shadow-lg border-0 bg-white/80 dark:bg-slate-800/80 backdrop-blur-sm">
          <CardHeader className="pb-6">
            <CardTitle className="text-2xl text-center text-slate-900 dark:text-slate-100">
              Cálculo do Déficit de Fluidos
            </CardTitle>
            <CardDescription className="text-center text-slate-600 dark:text-slate-400">
              Insira os dados do paciente para calcular o déficit de fluidos
            </CardDescription>
          </CardHeader>
          
          <CardContent>
            <form onSubmit={handleSubmit(onSubmit)} className="space-y-6">
              {/* Species Selection */}
              <div className="space-y-3">
                <Label className="text-base font-medium text-slate-900 dark:text-slate-100">
                  Espécie do Paciente
                </Label>
                <RadioGroup
                  value={watchedSpecies}
                  onValueChange={(value) => setValue('species', value as 'cao' | 'gato')}
                  className="flex gap-6"
                >
                  <div className="flex items-center space-x-2">
                    <RadioGroupItem value="cao" id="cao" />
                    <Label htmlFor="cao" className="text-slate-700 dark:text-slate-300 cursor-pointer">
                      Cão
                    </Label>
                  </div>
                  <div className="flex items-center space-x-2">
                    <RadioGroupItem value="gato" id="gato" />
                    <Label htmlFor="gato" className="text-slate-700 dark:text-slate-300 cursor-pointer">
                      Gato
                    </Label>
                  </div>
                </RadioGroup>
                {errors.species && (
                  <span className="text-sm text-red-600 dark:text-red-400">
                    {errors.species.message}
                  </span>
                )}
              </div>

              {/* Weight Input */}
              <div className="space-y-2">
                <Label htmlFor="weight" className="text-base font-medium text-slate-900 dark:text-slate-100">
                  Peso do Animal (kg)
                </Label>
                <Input
                  type="number"
                  step="0.1"
                  min="0"
                  id="weight"
                  placeholder="Ex: 5.5"
                  className="text-base h-12"
                  {...register("weight", { valueAsNumber: true })}
                />
                {errors.weight && (
                  <span className="text-sm text-red-600 dark:text-red-400">
                    {errors.weight.message}
                  </span>
                )}
              </div>

              {/* Dehydration Percentage */}
              <div className="space-y-2">
                <Label htmlFor="dehydration" className="text-base font-medium text-slate-900 dark:text-slate-100">
                  Percentual de Desidratação (%)
                </Label>
                <Input
                  type="number"
                  step="0.1"
                  min="0"
                  max="100"
                  id="dehydration"
                  placeholder="Ex: 8.0"
                  className="text-base h-12"
                  {...register("dehydration", { valueAsNumber: true })}
                />
                {errors.dehydration && (
                  <span className="text-sm text-red-600 dark:text-red-400">
                    {errors.dehydration.message}
                  </span>
                )}
                <p className="text-sm text-slate-500 dark:text-slate-400">
                  Valores típicos: Leve (3-5%), Moderada (6-8%), Severa (9-12%)
                </p>
              </div>

              {/* Submit Button */}
              <Button 
                type="submit" 
                className="w-full h-12 text-base font-medium"
                disabled={isCalculating}
              >
                {isCalculating ? 'Calculando...' : 'Calcular Déficit de Fluidos'}
              </Button>
            </form>
          </CardContent>
        </Card>

        {/* Results Card */}
        {result && (
          <Card className="mt-6 shadow-lg border-0 bg-green-50 dark:bg-green-900/20 backdrop-blur-sm">
            <CardHeader className="pb-4">
              <CardTitle className="text-xl text-green-800 dark:text-green-200 flex items-center justify-between">
                Resultado do Cálculo
                <Button 
                  variant="outline" 
                  size="sm"
                  onClick={resetCalculation}
                  className="text-green-700 border-green-300 hover:bg-green-100 dark:text-green-300 dark:border-green-600 dark:hover:bg-green-800"
                >
                  Novo Cálculo
                </Button>
              </CardTitle>
            </CardHeader>
            
            <CardContent className="space-y-4">
              <div className="grid grid-cols-1 md:grid-cols-3 gap-4 text-center">
                <div className="bg-white dark:bg-slate-800 p-4 rounded-lg">
                  <p className="text-sm text-slate-600 dark:text-slate-400">Espécie</p>
                  <p className="text-lg font-semibold text-slate-900 dark:text-slate-100">
                    {result.species}
                  </p>
                </div>
                <div className="bg-white dark:bg-slate-800 p-4 rounded-lg">
                  <p className="text-sm text-slate-600 dark:text-slate-400">Peso</p>
                  <p className="text-lg font-semibold text-slate-900 dark:text-slate-100">
                    {result.weight} kg
                  </p>
                </div>
                <div className="bg-white dark:bg-slate-800 p-4 rounded-lg">
                  <p className="text-sm text-slate-600 dark:text-slate-400">Desidratação</p>
                  <p className="text-lg font-semibold text-slate-900 dark:text-slate-100">
                    {result.dehydration}%
                  </p>
                </div>
              </div>
              
              <div className="bg-green-100 dark:bg-green-800/30 p-6 rounded-lg text-center">
                <p className="text-sm text-green-700 dark:text-green-300 mb-2">
                  Déficit de Fluidos Calculado
                </p>
                <p className="text-3xl font-bold text-green-800 dark:text-green-200">
                  {result.fluidDeficit.toFixed(1)} mL
                </p>
                <p className="text-sm text-green-600 dark:text-green-400 mt-2">
                  ({(result.fluidDeficit / 1000).toFixed(2)} L)
                </p>
              </div>

              <div className="bg-blue-50 dark:bg-blue-900/20 p-4 rounded-lg">
                <h4 className="font-medium text-blue-800 dark:text-blue-200 mb-2">
                  Informações Importantes:
                </h4>
                <ul className="text-sm text-blue-700 dark:text-blue-300 space-y-1">
                  <li>• Este cálculo representa apenas o déficit de fluidos</li>
                  <li>• Considere também fluidos de manutenção e perdas contínuas</li>
                  <li>• A reposição deve ser feita gradualmente (geralmente 24-48h)</li>
                  <li>• Monitore sinais vitais durante a fluidoterapia</li>
                </ul>
              </div>
            </CardContent>
          </Card>
        )}

        {/* Footer */}
        <div className="text-center mt-8 text-sm text-slate-500 dark:text-slate-400">
          <p>Fórmula: Peso (kg) × % Desidratação × 10 = Déficit (mL)</p>
        </div>
      </div>
    </div>
  );
}
